import React, {useEffect, useState, useMemo } from 'react';
import { useParams } from "react-router-dom";
import AuthorProfile from "../AuthorProfile/AuthorProfile";
import {useHistory, withRouter, Link} from 'react-router-dom';
import {connect, RootStateOrAny, useSelector} from 'react-redux';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useWallet } from '@binance-chain/bsc-use-wallet';
import axios from 'axios';
import { getBalanceNumber } from 'utils/formatBalance';
import { decimal } from 'utils/decimal';
import { symbol } from 'utils/symbol';
import { useKaiECOERC721Contract, useKaiECOMarketplaceContract } from 'hooks/useContract';
import { getKaiECOMarketplaceAddress } from 'utils/addressHelpers';

const BASE_URL = "https://my-json-server.typicode.com/themeland/netstorm-json-1/activity";


function Profile(props) {
    const [image, setImage] = useState(null);
    const history = useHistory();
    const { wallet } = useParams();
    const [walletaddress, setWalletAddress] = useState('');
    const [myTokens, setMyTokens] = useState([]);

    const collections = useSelector((state: RootStateOrAny) => state.marketplace.collections);
    const myCollecitons = collections.find(c => c.creator === wallet);
    const marketplaceContract = useKaiECOMarketplaceContract(getKaiECOMarketplaceAddress());
    const tokens = useSelector((state: RootStateOrAny) => state.marketplace.items)
    //const saleTokens = useMemo(tokens?.filter((token) => token.owner === wallet.account && token.sale === 0),[tokens]);
    
    const user  = useSelector((state: RootStateOrAny) => state.marketplace.user);

    


    useEffect(() => {
        console.log(wallet);
        if(wallet === null){
            console.log(wallet);
            history.push('/connect');
        }else{
            setWalletAddress(wallet);
        }
        
    })

    const handleUpload = (event) =>{
        if (event.target.files && event.target.files[0]) {
            setImage(URL.createObjectURL(event.target.files[0]));
        }
    }

    
    return(
        <section className="author-area  activity-area">
            <div className="container">
                <div className="row justify-content-between">
                    <div className="col-12 col-md-12">
                        <div className="card no-hover text-center" style={{padding:'0.5rem'}}>
                            <div className="image-over">
                               
                                <img className="card-img-top" style={{maxHeight : '300px', height: 'auto', width: '100%', maxWidth:'1124px', backgroundSize:'cover'}} src={image === null?'/img/blog_8.jpg':image}  alt="" />
                                
                                {/* Author */}
                                <div className="author">
                                    <div className="author-thumb avatar-lg">
                                        <img className="rounded-circle" src={user?.image != null ?`https://ipfs.io/ipfs/${user.image}`: '/img/user.jpg'} alt="" />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className='row justify-content-between' style={{marginTop:'5rem'}}>
                    <div className='col-12 col-md-12 text-center'>
                    <button type="button" className='btn btn-bordered-white'>{walletaddress}</button>
                    </div>
                </div>
                
                <div className='row justify-content-between'>
                    <div className='col-12 text-center mt-4'>
                        <Link className='btn btn-bordered' to="/setting" >Edit Profile</Link>
                    </div> 
                </div>
                <div className='row items justify-content-between  mt-4'>
                    <div className='col-12 col-md-12 col-lg-12'>
                        <ul className="netstorm-tab nav nav-tabs" id="nav-tab" style={{paddingLeft:'33%'}}>
                            <li>
                                <a className="active" id="nav-home-tab" data-toggle="pill" href="#nav-sale">
                                    <h5 className="m-0">My Items</h5>
                                </a>
                            </li>
                        </ul>
                        <div className="tab-content" id="nav-tabContent">
                            <div className="tab-pane fade show active" id="nav-sale">
                                <div className='row items'>
                                {myTokens?.map((item, idx) => {
                                    return (
                                        <div key={`exo_${idx}`} className="col-12 col-sm-6 col-lg-3 item">
                                            <div className="card">
                                                <div className="image-over">
                                                    <Link to={`/token/${item.contract}/${item.tokenId}`}>
                                                        <img className="card-img-top" src={`${item.image}`} alt="" />
                                                    </Link>
                                                </div>
                                                {/* Card Caption */}
                                                <div className="card-caption col-12 p-0">
                                                    {/* Card Body */}
                                                    <div className="card-body">
                                                        <a href="/item-details">
                                                            <h5 className="mb-0">{item.name}</h5>
                                                        </a>
                                                        <div className="seller d-flex align-items-center my-3">
                                                            <span>{item.description}</span>
                                                        </div>
                                                        <div className="card-bottom d-flex justify-content-between">
                                                            <span>{ getBalanceNumber(item.price, decimal(item.token)) } {symbol(item.token)}</span>
                                                            <span>{new Date(item.auctionTime * 1000)}</span>
                                                        </div>
                                                        
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    );
                                })}
                                </div>
                                
                            </div>
                        </div>
                    </div> 
                </div>
            </div>
            <ToastContainer />
        </section>
    )
}

const mapStateToProps = state => ({
    ...state
  });
  

  export default connect(
    mapStateToProps,
    
  )(withRouter(Profile));
